.DisplayTask {
  width: 40%;
  background-color: white;
  border-radius: 30px;
  padding: 13px;
  display: flex;
  justify-content: space-between;
  gap: 20px;
  animation: color-change 3s infinite ease-in;
}

@keyframes color-change {
    0% {
        background-color: rgba(255, 255, 255, 0.651);
    }

    50% {
        background-color: rgb(249, 230, 169);
    }

    100% {
        background-color: rgba(255, 255, 255, 0.651);
    }
}

.container-task {
    display: flex;
    gap: 15px;
}

.check-box {
  cursor: pointer;
  margin-left: 20px;
  position: relative;
  top: 2.5px;
  width: 18px; /* Desired width */
  height: 18px; /* Desired height */
  -webkit-appearance: none; /* Disable default appearance */
  -moz-appearance: none;
  appearance: none;
  border: 2px solid black; /* Checkbox border color */
  border-radius: 4px; /* Checkbox border radius */
  outline: none; /* Remove focus outline */
  background-color: rgba(0, 0, 0, 0); /* Checkbox background color */
}

.check-box::before {
  content: "\2713"; /* Unicode character for checkmark */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: 1000;
  font-size: 14px; /* Adjust the size of the checkmark */
  color: rgb(0, 0, 0); /* Color of the checkmark */
  opacity: 0; /* Initially invisible */
}

.check-box:checked::before {
  opacity: 1; /* Show the checkmark when the checkbox is checked */
}

.task-style {
  font-size: 1.4rem;
  text-transform: capitalize;
  font-family: "Courier New", Courier, monospace;
  font-weight: 1000;
  text-decoration: none;
}

.task-style.checked {
    text-decoration: line-through;
  }

.delete-icon {
    text-align: end;
    padding-right: 10px;
    font-weight: 1000;
    cursor: pointer;
}
